import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

import Menu from "./menu"

const Header = ({ siteTitle }) => (
  <header
    style={{
      background: `#4287f5`,
      top: 0,
      width: `100vw`,
      position: `fixed`,
      maxHeight: `5.0rem`,
    }}
  >

    <div
      style={{
        margin: `0 auto`,
        padding: `1.0rem 1.0rem`,
        height: `5.0rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: `white`,
            textDecoration: `none`,
          }}
        >
          {siteTitle}
        </Link>
      </h1>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
