import React from "react"

const Footer = () => (
    <footer
        style={{
            background: `#4287f5`,
            margin: `0`,
            bottom: 0,
            width: `100vw`,
            position: `fixed`,
        }}
    >
        <div
            style={{
                margin: `0`,
                padding: `1.45rem 1.0875rem`,
            }}
        >
             {new Date().getFullYear()}, Built by Graham Long, with
          {` `}
            <a href="https://www.gatsbyjs.org">Gatsby</a>
        </div>
    </footer>
)

export default Footer